import axios from 'axios';

const baseUrl = 'http://localhost:8090/api'

//회원가입부분

function getUser() {
    return axios.get(baseUrl + "/join")
}

//회원가입
function insertUser(data) {
    return axios.post(baseUrl + "/join", data, {
        headers: {
            "Content-Type": `application/json`,
        }
    });
}

export {
    insertUser,
    getUser
}
